* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

html {
  scroll-behavior: smooth;
}

body {
  background: #d6ceb9;
  height: 100vh;
  display: flex;
}

section {
  padding: 25rem 7%;
  display: block;
}

header {
  z-index: 1000;
  background: #7a7a7a91;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100px;
  display: flex;
  position: fixed;
  box-shadow: 0 0 10px #000;
}

#chk1 {
  display: none;
}

i {
  color: #060606;
  cursor: pointer;
}

header .logo a {
  color: #5b0000;
  text-shadow: 1px 1px 2px #4a4a4a, 0 0 25px #850000, 0 0 5px #7d5d5d;
  text-decoration: none;
}

header .logo a :hover {
  text-shadow: 1px 1px 2px #e8e8e8, 0 0 25px #d5d5d5, 0 0 5px #a09c9c;
}

header .logo {
  flex: 1;
  margin-left: 50px;
}

header .logo :hover {
  text-shadow: 1px 1px 2px #e8e8e8, 0 0 25px #d5d5d5, 0 0 5px #a09c9c;
}

header .logo span {
  color: #dedede;
}

header .search-box {
  flex: 1;
  position: relative;
}

.search-box input {
  color: #222;
  text-align: center;
  background: #dedede;
  border-radius: 30px;
  outline: none;
  width: 100%;
  height: 40px;
  font-size: 16px;
}

.search-box button {
  cursor: pointer;
  background-color: #00000064;
  border: none;
  border-radius: 30px;
  width: 40px;
  height: 40px;
  position: absolute;
  top: 0;
  right: 0;
  transform: scale(.9);
}

header ul {
  flex: 2;
  justify-content: space-evenly;
  display: flex;
}

header ul li {
  list-style: none;
}

header ul li a {
  color: #420909;
  padding: 2px 4px;
  font-size: larger;
  font-weight: 700;
  text-decoration: none;
}

header ul li a:hover {
  text-shadow: 1px 1px 2px #4a4a4a, 0 0 25px #850000, 0 0 5px #7d5d5d;
  border-bottom: 2px solid #6b1212;
}

header .menu {
  font-size: 2.5rem;
  display: none;
  position: relative;
  left: -1rem;
}

header .menu :hover {
  text-shadow: 1px 1px 2px #4e4e4e, 0 0 25px #850000, 0 0 5px #7d5d5d;
}

@media screen and (width <= 450px) {
  .search-box button {
    position: absolute;
  }

  header ul {
    background: #664545d1;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: calc(100vh - 100px);
    transition: right .5s linear;
    position: fixed;
    top: 100px;
    right: -100%;
  }

  header .menu {
    text-align: center;
    width: 50px;
    display: block;
  }

  #chk1:checked ~ ul {
    right: 0;
  }

  header ul li a {
    font-size: medium;
  }
}

@media screen and (width >= 451px) and (width <= 768px) {
  .search-box button {
    position: absolute;
  }

  header ul {
    background: #664545d1;
    flex-direction: column;
    align-items: center;
    width: 60%;
    height: calc(100vh - 100px);
    transition: right .5s linear;
    position: fixed;
    top: 100px;
    right: -100%;
  }

  header .menu {
    text-align: center;
    width: 70px;
    display: block;
  }

  #chk1:checked ~ ul {
    right: 0;
  }

  header ul li a {
    font-size: larger;
  }
}

@media screen and (width >= 769px) and (width <= 1024px) {
  header ul {
    width: 50%;
  }

  .search-box input {
    font-size: 16px;
  }

  header ul li a {
    font-size: larger;
  }
}
